.container {
  width: 75%;
  height: 80%;
  display: grid;
  grid-template-columns: 1.7fr 9fr 1fr;
}

.mobileContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.contentBackground {
  width: 100%;
  height: 100%;
  background-color: white;
  border: 1px solid black;
  z-index: 1;
  border-radius: 0 0 10px 10px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  position: relative;
  overflow-y: overlay;
  overflow-x: hidden;
  display: grid;
}

.mobileContentBackground {
  width: 100%;
  height: 100%;
  background-color: white;
  position: relative;
  z-index: 1;
  overflow-y: overlay;
  overflow-y: auto;
}

.contentContainer {
  margin: 50px;
}

@media (max-width: 1600px) {
  .container {
    width: 90%;
  }
  .contentContainer {
    margin: 30px;
  }
}

@media (max-width: 1024px) {
  .container {
    width: 95%;
  }
  .contentContainer {
    margin: 30px 15px;
  }
}
