.navigationContainer {
  height: 60%;
  min-height: 370px;
  min-width: 100px;
  background-color: var(--main);
  border-radius: 0 10px 10px 0;
  border: solid var(--outerBorder);
  border-width: 1px 1px 1px 0px;
  margin-left: 1px;
  color: var(--textColor);
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  box-shadow: 4px 4px 4px 0px rgb(0 0 0 / 25%);
  z-index: 1;
}

.mobileNavigationContainer {
  height: 60px;
  width: 100%;
  display: flex;
  color: var(--textColor);
  background-color: var(--main);
  z-index: 1;
  position: fixed;
  bottom: 0;
  left: 0;
}

.mobileNavigationContainer .buttonContainer {
  font-size: 14px;
}

.mobileNavigationContainer .buttonContainer img {
  height: 22px;
}

.buttonContainer {
  width: 100%;
  height: 100%;
  display: flex;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 16px;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;
}

.buttonContainer img {
  filter: var(--textColorFilter);
}
